* {
    box-sizing: border-box;
    /* border: 1px solid red; */
}

body {
    margin: 0;
    font-family: 'Poppins', sans-serif;
    background: #F5F7FB;
}

#root {
    margin: 0;
    padding: 0;
}

.main-container{
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    min-height: 100vh;
    max-width: 100%;
}

main {
    display: flex;
    flex-direction: column;
    margin-top: 2em;
}

.yellowblob {
    z-index: -2;
    position: absolute;
    width: 400px;
    top: 0;
    right: 0;
}

.blueblob {
    z-index: -2;
    position: absolute;
    width: 200px;
    bottom: 0;
    left: 0;
}

/*******Welcome Screen *********/

.welcome {
    text-align: center;
    color: #293264;

}

.title {
    font-size: 2.5rem;
}

.start-btn,
.score-btn {
    width: 193px;
    height: 52px;
    left: 179px;
    background: #4D5B9E;
    border-radius: 15px;
    border: none;
    color: #F5F7FB;
    font-size: 1.5rem;
    margin-top: 1.25em;
    cursor: pointer;
    padding: 1rem;
}

.start-btn:focus {
    outline: none;
}

.start-btn:active {
    box-shadow: inset 5px 5px 10px -3px rgba(0, 0, 0, 0.7);
}

main {
    width: 80%;
    max-width: 600px;
    /* border: 1px solid red; */
}

/*******Question component*********/

.question-container {
    max-width: 100%;
    color: #293264;
    padding: .5em;
    margin: .5;
    /* border: 1px solid red; */
}

.question--title {
    font-size: 1rem;
    font-weight: bold;
    margin-top: 0;
    margin-bottom: .25em;
}

.question-options {
    display: flex;
    flex-wrap: wrap;
}

.option {
    text-align: center;
    border: 1px solid #293264;
    border-radius: 10px;
    min-width: 100px;
    padding: .5em;
    margin-right: 1.25em;
    cursor: pointer;
    font-weight: bold;
    font-size: 11px;
}

hr {
   color: #4D5B9E;
    margin: .75em 0;
}

.score-btn {
    margin: 1em auto;
}

.footer-container {
    display: flex;
    align-items: center;
}

.score {
    font-size: 1.5rem;
    color: #293264;
    font-weight: bold;
    margin-left: .5em;
}

@media screen and (max-width:1000px){
    .yellowblob {
        width: 150px;
    }

    .blueblob {
        width: 100px;
    }
}

@media screen and (max-width:560px){
    .score {
        font-size: 1rem;
    }
    .score-btn {
        font-size: 1rem;
    }

    .yellowblob {
        width: 100px;
    }

    .blueblob {
        width: 100px;
    }
}
